#soft-skills-block {
    position: absolute;
    width: 50vw;
    height: 70vh;
    display: none;
    bottom: 100px;
  }
  #soft-skills-content {
    width: 85%;
    height: 80%;
    margin: 20px auto;
  
  }
  #soft-skills-wheel {
    width: 500px;
    height: 400px;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    -ms-align-items: center;
    align-items: center;
  }
  #soft-skills-wheel p {
    text-align: center;
    position: absolute;
    padding: 0;
    margin: 0;
  }
  #soft-skills-wheel p:nth-child(2) {
    top: 10px;
  }
  #soft-skills-wheel p:nth-child(3) {
    right: 70px;
    top: 80px;
  }
  #soft-skills-wheel p:nth-child(4) {
    right: 0;
  }
  #soft-skills-wheel p:nth-child(5) {
    bottom: 60px;
    right: 20px;
  }
  #soft-skills-wheel p:nth-child(6) {
    bottom: 10px;
  }
  #soft-skills-wheel p:nth-child(7) {
    bottom: 60px;
    left: 60px;
  }
  #soft-skills-wheel p:nth-child(8) {
    left: 35px;
  }
  #soft-skills-wheel p:nth-child(9) {
    left: 90px;
    top: 70px;
  }