body {
  margin: 0;
  padding: 0;
  /* background-color: #000; */
  background-color: transparent;
  overflow: hidden;
}
.clear-floats {
  clear: both;
}
h3 {
  text-align: center;
  margin: 0;
}
h4 {
  margin: 10px 0;
}
hr {
  width: 400px;
  height: 20px;
  border: none;
  margin: 0 auto;
  background: url(img/line1.png);
  background-size: 100% 100%;
}
#wrapper {
  width: 100vw;
  height: 100vh;
   background: url(img/bgDay.png) center no-repeat; 
   background-size: cover; 
  background-color: transparent;
  display: flex;
  justify-content: center;
  -ms-align-items: flex-end;
  align-items: flex-end;
  overflow: hidden;
}
#language-board {
  position: absolute;
  left: 5%;
  bottom: 45px;
  width: 280px;
  height: 400px;
  transform: perspective(500px) rotateY(30deg);
  background: url(img/languageBoard-Copy.png) center no-repeat;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
}
#language-elements {
  margin-top: 20px;
  text-align: center;
}
#en, #ua, #ru {
  width: 200px;
  height: 50px;
  display: flex;
  transition: transform .3s ease;
  justify-content: center;
  -ms-align-items: center;
  align-items: center;
}
#en {
  background: url(img/plank1.png) center no-repeat;
  background-size: 100% 100%;
}
#ua {
  background: url(img/plank2.png) center no-repeat;
  background-size: 100% 100%;
}
#ru {
  background: url(img/plank3.png) center no-repeat;
  background-size: 100% 100%;
}
#en:hover, #ua:hover, #ru:hover, .active {
  transform: scale(1.15);
}
#language-elements p {
  margin: 0;
  margin-bottom: 5px;
  cursor: pointer;
  font-family: 'Lobster', cursive;
  font-size: 25px;
  letter-spacing: 3px;
  /*color: ;*/
}
#light-shock {
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  position: absolute;
  display: none;
}
#main-board-menu {
  width: 620px;
  height: 450px;
  /* margin-bottom: -20px; */
  
  margin-bottom: 45px;
  background: url(img/mainMenuBoard.png) center no-repeat;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  -ms-align-items: center;
  align-items: center;
  /* transform: scale(.5); */

}
#container {
  margin-top: 30px;
  display: grid;
  grid-template-columns: 150px 150px 150px;
  grid-template-rows: 100px 100px 90px;
  grid-gap: 10px;
}
#personal-paper, #skills-paper, #language-paper, #soft-skills-paper, #portfolio-paper {
  width: 150px;
  height: 100px;
  cursor: pointer;
  background: url(img/wantedPapper.png) center no-repeat;
  background-size: 100% 100%;
  text-align: center;
  /*font-family: 'Lobster', cursive;*/
  display: flex;
  justify-content: center;
  -ms-align-items: center;
  align-items: center;
  transition: transform .3s ease;
}
#personal-paper:hover, #skills-paper:hover, #language-paper:hover, #soft-skills-paper:hover, #portfolio-paper:hover {
  transform: scale(1.3);
}
.layer-content {
  background: url(img/personalInfBlockBG.jpg);
  background-size: 100% 100%;
  border-radius: 10px;
  box-shadow: 0 0 15px 10px #B28841;
}
#title {
  text-align: center;
}
#title h2 {
  margin-bottom: 0;
}
#title hr {
  width: 400px;
  height: 20px;
  border: none;
  margin: 0 auto;
  background: url(img/line1.png);
  background-size: 100% 100%;
}
.close-button {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 15px;
  right: 4%;
  background: url(img/exit1.png);
  background-size: 100% 100%;
  border-radius: 50%;
  cursor: pointer;
}
.close-button:hover {
  background: url(img/exit2.png);
  background-size: 100% 100%;
  /*transform: rotate(180deg);*/
}



#language-skills-block {
  position: absolute;
  width: 70vw;
  height: 70vh;
  display: none;
  bottom: 100px;
}
@keyframes blockappear {
  0% {
      transform: perspective(1000px) rotateX(90deg);
  }
  100% {
      transform: perspective(1000px) rotateX(0deg);
  }
}
@keyframes blockhide {
  0% {
      transform: perspective(1000px) rotateX(0deg);
  }
  100% {
      transform: perspective(1000px) rotateX(-90deg);
  }
}
@keyframes lightshock {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 1;
  }
  100% {
      opacity: 0;
  }
}