#personal-information-block {
    position: absolute;
    width: 70vw;
    height: 70vh;
    display: none;
    bottom: 100px;
}
#personal-information-content {
    width: 85%;
    height: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    -ms-align-items: center;
    align-items: center;
}
#image-block {
    width: 300px;
    height: 400px;
    text-align: center;
}
#image-block img {
    width: 250px;
    height: 300px;
}
#info-block {
    width: 500px;
    height: 200px;
}
#info-block hr {
    width: 250px;
}
#info-block table {
    margin: 0 auto;
}
#info-block td {
    width: 100px;
}
#info-block tr {
    height: 25px;
}
#education-block {
    width: 500px;
    height: 200px;
}
#education-block hr {
    width: 150px;
}
#education-block p {
    margin: 0;
    margin-bottom: 20px;
}
#education-block span {
    color: grey;
}