#technical-skills-block {
    position: absolute;
    width: 70vw;
    height: 70vh;
    display: none;
    bottom: 100px;
  }
  #technical-skills-content {
    width: 85%;
    height: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    -ms-align-items: center;
    align-items: center;
  }
  #technical-skills-content tr {
    height: 40px;
  }
  #front {
    width: 250px;
    height: 80%;
  }
  #back {
    width: 250px;
    height: 80%;
  }
  #other {
    width: 250px;
    height: 80%;
  }
  #front hr, #back hr, #other hr {
    width: 150px;
  }
  .skills-list {
    list-style: none;
    padding: 0;
  }
  .skills-list li {
    margin-bottom: 20px;
  }
  .skills-list p {
    font-size: 18px;
    font-weight: 700;
    margin: 0;
  }
  .progress {
    background-color: #111;
    height: 15px;
    width: 100%;
    border-radius: 5px;
    position: relative;
    padding: 4px;
  }
  #html5>.fill {
    background-color: #4d089a;
    width: 60%;
  }
  #css3>.fill {
    background-color: #323edd;
    width: 50%;
  }
  #js>.fill {
    background-color: #dc2ade;
    width: 30%;
  }
  #jquery>.fill {
    background-color: #ed6663;
    width: 20%;
  }
  #java>.fill {
    background-color: #5b8c5a;
    width: 70%;
  }
  #python>.fill {
    background-color: #ea728c;
    width: 30%;
  }
  #sql>.fill {
    background-color: #fc9d9d;
    width: 40%;
  }
  .number {
    position: absolute;
    top: 0;
    color: #fff;
    padding: 3px 5px;
    font-size: 15px;
    font-weight: 600;
  }
  #html5>.number {
    right: 25%;
  }
  #css3>.number {
    right: 35%;
  }
  #js>.number {
    right: 55%;
  }
  #jquery>.number {
    right: 65%;
  }
  #java>.number {
    right: 15%;
  }
  #python>.number {
    right: 55%;
  }
  #sql>.number {
    right: 45%;
  }
  .fill {
    background: linear-gradient(45deg, rgba(255, 255, 255, 0.5) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0.5) 75%, transparent 75%, transparent);
    background-size: 40px;
    border-radius: 5px;
    height: 100%;
    animation: move .5s linear infinite;
  }

  @keyframes move {
    100% {
        background-position: 40px 0;
    }
  }